.order{display:flex;flex-direction:column;align-items:center;letter-spacing:1px;min-height:400px;max-height:400px;overflow:auto;padding:0 2rem}.order img{width:220px}.order .choose{display:flex;align-items:center;justify-content:space-between;width:100%;margin:.3rem 0}.order .choose .title{margin-right:1rem;min-width:25px}.order .choose .radioBtn{display:flex;align-items:center;padding-right:1rem}.order input{padding:.3rem;margin:.5rem 0;border-radius:0}.order .select{width:80%;letter-spacing:1px;position:relative}.order .select select{width:100%;padding:.5rem;font-family:"微軟正黑體";font-size:14px;cursor:pointer}.order .select::before{position:absolute;content:"";top:18px;right:10px;width:0;height:0;border:6px solid rgba(0,0,0,0);border-color:#333 rgba(0,0,0,0) rgba(0,0,0,0) rgba(0,0,0,0)}.order select,.order input[type=radio],.order input[type=number]{appearance:none;border:1px solid #303030}.order input[type=radio]{width:15px;height:15px;border:1px solid #333;border-radius:50%;margin-right:.5rem;cursor:pointer}.order input[type=radio]:checked{background:#333}.order .numInput{width:180px;text-align:center;appearance:textfield}.order .numInput::-webkit-inner-spin-button,.order .numInput::-webkit-outer-spin-button{appearance:none}.order .numBtn{width:34px;height:34px;background:#303030;color:#fff;font-weight:bold;font-size:18px;display:flex;justify-content:center;align-items:center;border:none;cursor:pointer}.order .numBtn.disabledBtn{background:#636363;cursor:default}.order .numberTip{text-align:right;width:100%;font-size:14px;color:#d81010}